<template>
  <div class="container">
    <div class="content">
      <InputStep v-if="isInput" @search="clickSearch" />
      <div v-else>
        <IngStep is-all-done="isAllDone" />
        <div v-loading="loading" class="content-info">
          <ct-section-title class="content-info-title" :title="$t('linkD.basicInfo')" />
          <el-row :gutter="20">
            <el-col
              v-for="(obj, index) in infoList"
              :key="obj.prop"
              :span="index === infoList.length - 1 ? 24 : 4"
            >
              <CtTooltip :label="obj.label" :content="itemInfoFormatter(obj)"></CtTooltip>
            </el-col>
          </el-row>
        </div>
        <div class="seperator"></div>
        <div class="content-info">
          <ct-section-title
            class="content-info-title"
            :title="$t('linkD.resultDetailTitle')"
          />
          <div v-for="obj in resultList" :key="obj.prop" class="content-info-item">
            <img
              class="dia-img"
              :src="`${require('@/assets/image/dia/' + obj.img + '.png')}`"
            />
            <div class="content-info-item-type">
              {{ obj.label }}
              <el-tooltip effect="dark" :content="obj.content" placement="top">
                <svg-icon class="tip-icon" icon="tip" width="16" height="16"></svg-icon>
              </el-tooltip>
            </div>
            <div class="content-info-item-detail">
              <div>
                {{ obj.formatter(obj.raw) }}
                <!-- <svg-icon class="tip-icon" icon="stop" width="16" height="16"></svg-icon> -->
              </div>
              <div v-loading="obj.loading" class="des"></div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import fetch from '@/request/fetch'
import InputStep from './componnets/InputStep'
import IngStep from './componnets/IngStep'
import { statusOptions } from '@/utils/constant'
export default {
  components: {
    InputStep,
    IngStep,
  },
  data() {
    return {
      isInput: true,
      loading: false,
      isAllDone: false,
      baseInfoRes: {},
      resultList: [
        {
          label: this.$t('linkD.resutlItemLabel1'),
          itemType: 'SIM_STATUS_CMP',
          img: 'dia_ing_1',
          formatter: (raw) => {
            const item = statusOptions.find((item) => item.value === raw.status)
            if (item) {
              return this.$t('simMag.' + item.key)
            }
            return raw.statusDes
          },
          content: this.$t('linkD.resutlItemContent1'),
          loading: true,
          raw: {},
        },
        {
          label: this.$t('linkD.resutlItemLabel2'),
          itemType: 'SIM_STATUS_CONNECTION',
          img: 'dia_ing_2',
          content: this.$t('linkD.resutlItemContent2'),
          loading: true,
          raw: {},
          formatter: (raw) => {
            return raw.statusDes
          },
        },
      ],
    }
  },
  computed: {
    infoList() {
      return [
        {
          label: 'ICCID',
          prop: 'iccid',
        },
        {
          label: 'MSISDN',
          prop: 'msisdn',
        },
        {
          label: 'IMSI',
          prop: 'imsi',
        },
        {
          label: this.$t('simMag.cust'),
          prop: 'custName',
        },
        {
          label: this.$t('simMag.openDate'),
          prop: 'createDate',
        },
        {
          label: this.$t('simMag.actDate'),
          prop: 'actDate',
        },
        {
          label: this.$t('simMag.addressId'),
          prop: 'supplier',
        },
      ]
    },
  },
  methods: {
    clickSearch(param) {
      this.qryConnectDiagnosisBaseInfo(param)
    },
    async qryConnectDiagnosisBaseInfo(param) {
      this.loading = true
      const res = await fetch('/equipment/qryConnectDiagnosisBaseInfo', param, {
        method: 'get',
      }).finally(() => (this.loading = false))
      this.baseInfoRes = res
      this.isInput = false
      this.getAll()
    },
    async getAll() {
      const all = await Promise.all([
        this.qrySelectPackageByProdInst('SIM_STATUS_CMP'),
        this.qrySelectPackageByProdInst('SIM_STATUS_CONNECTION'),
      ])
      this.resultList.forEach((obj) => {
        this.isAllDone = true
        all.forEach((obj2) => {
          if (obj.itemType === obj2.itemType) {
            obj.raw = obj2
            obj.loading = false
          }
        })
      })
      this.resultList = [...this.resultList]
    },
    async qrySelectPackageByProdInst(itemType) {
      try {
        const param = { msisdn: this.baseInfoRes.msisdn, itemType }
        const res = await fetch('/equipment/qryConnectDiagnosisItem', param, {
          method: 'post',
        })
        return res
      } catch (e) {}
    },
    itemInfoFormatter(obj) {
      const des = obj.formatter
        ? obj.formatter(this.baseInfoRes[obj.prop])
        : this.baseInfoRes[obj.prop] ?? '-'
      return des
    },
  },
}
</script>
