    }
    ::v-deep .el-col:not(:nth-last-child(-n + 1)) {
      margin-bottom: 35px;
    }

    &-item {
      display: flex;
      align-items: center;
      margin-top: 30px;

      &-type {
        margin-left: 30px;
        width: 200px;
        margin-right: 200px;
      }

      &-detail {
        .des {
          margin-top: 4px;
